import React from 'react'
import Icon from 'components/Icon-svg'
import { Local } from 'utils/storage'
import connect from 'connect'
import {withRouter} from 'react-router-dom'
import { SearchBar, Button, WhiteSpace, WingBlank, Carousel, List, Grid, Card, TabBar } from 'antd-mobile';
@connect
@withRouter
export default class extends React.Component {
    state = {
        value: '',
        pageindex: 1,
    }
    handleChange (value) {
        this.setState({
            value
        })
    }
    async getSearchData () {
        const {pageindex, value} = this.state
        const {refreshSearchList} = this.props
        await refreshSearchList({
            token: '',
            name: value
        })
        this.setState({
            value: ''
        })
        
    }
    render () {
        return (
            <div className="search-head-wrapper df-sb">
                <div className="search-title df-sb border-half-bottom">
                    <div className="search-btn" onClick={this.getSearchData.bind(this)}>
                        <img src={require('../../../images/back.png')} alt="LOGO" className="back"  onClick={e => {this.props.history.goBack()}}/>
                    </div>
                    <div className="search">
                        <SearchBar
                                placeholder="古风水墨画"
                                cancelText="搜索"
                                onClear={value => console.log(value, 'onClear')}
                                onFocus={this.onfocusin}
                                onBlur={() => console.log('onBlur')}
                                onChange={() => console.log('change')}
                              />
                        
                    </div>
                </div>
            </div>
        )
    }
}


